.container{
    padding:10px;
    max-width: fit-content;
    margin:50px auto;
  }
  .btn{
    border:none;
    padding:7px 10px;
    font-size: 18px;
    border-radius: 5px;
    cursor:pointer;
  }
  .btn-add{
    background: #058739;
    color:white;
    margin-left: 90.5%;
    width:100px;
  }
  .addContainer{
    position:absolute;
    background-color: rgba(0,0,0,0.2);
    left:0;
    right:0;
    bottom:0;
    top:0;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .addContainer form{
    width :420px;
    background-color: white;
    display: flex;
    flex-direction: column;
    padding:50px 40px;
    box-shadow: 5px 5px 10px rgba(0,0,0,0.2);
    border-radius: 5px;
  }
  .addContainer form label{
    font-size: 18px;
  }
  .addContainer form input{
    font-size: 18px;
    padding: 5px;
    margin-top: 5px;
    margin-bottom: 10px;
  }
  .addContainer form button{
    background-color: royalblue;
    color:white;
    font-weight:500;
    margin-top:10px
  }
  .addContainer form .close-btn{
    margin-left: auto;
    font-size: 18px;
    width: 20px;
    height: 20px;
    display:flex;
    justify-content: center;
    align-items: center;
    border-radius: 20px;
    border:1px solid black;
    cursor: pointer;
  }
  .tableContainer{
    margin-top: 25px;
    box-shadow: 7px 7px 10px rgba(0,0,0,0.2);
    border-radius: 5px;
    background-color: #faf9f7;
  }
  .tableContainer table thead{
    background-color: #03193b;
    color:white
  }
  .tableContainer table thead tr th,
  .tableContainer table tbody tr td{
    min-width: 200px;
    padding: 7px;
    text-align: center;
  }
  .tableContainer table tbody tr td{
    border-bottom: 1px solid #c9c5c2;
  }
  .btn-edit,
  .btn-delete{
    font-size: 16px;
    padding: 5px 10px;
    margin:0px 10px;
  }
  .btn-edit{
    background-color:#fa8525;
    color: white;
  }
  .btn-delete{
    background-color:#a8053e ;
    color:white;
  }